import { t } from "@lingui/macro"
import React, { useMemo, useCallback } from "react"
import { atom, useRecoilState } from "recoil"
import styled from "styled-components/macro"

import { skipTr, staticTr } from "lib/lingui"
import { WebData } from "store/tango"

import { flex } from "../../lib/flex"

const $devSettings = atom<{
  game: NonNullable<WebData["game"]> | null
  sdkVersion: string | null
  cliVersion: string | null
  visible: boolean
  challengeNumber: number
}>({
  key: "$devSettings",
  default: {
    game: null,
    sdkVersion: null,
    cliVersion: null,
    visible: false,
    challengeNumber: 1,
  },
})

export function useDevSettings() {
  const [devSettings, setSettings] = useRecoilState($devSettings)

  return useMemo(
    () => ({
      ...devSettings,
      enabled: !!devSettings.game,
      setCliVersion: (cliVersion: string) => {
        setSettings({
          ...devSettings,
          cliVersion,
        })
      },
      setSdkVersion: (sdkVersion: string) => {
        setSettings({
          ...devSettings,
          sdkVersion,
        })
      },
      setGame: (game: NonNullable<WebData["game"]>) => {
        setSettings((settings) => ({
          ...settings,
          game,
        }))
      },
      setVisible: (visible: boolean) =>
        setSettings({
          ...devSettings,
          visible,
        }),
      incrementChallengeNumber: () =>
        setSettings({
          ...devSettings,
          challengeNumber: devSettings.challengeNumber + 1,
        }),
      decrementChallengeNumber: () =>
        setSettings({
          ...devSettings,
          challengeNumber: devSettings.challengeNumber - 1,
        }),
    }),
    [setSettings, devSettings]
  )
}

export function DevSettings() {
  const {
    visible,
    setVisible,
    challengeNumber,
    incrementChallengeNumber,
    decrementChallengeNumber,
    sdkVersion,
    cliVersion,
  } = useDevSettings()

  const hide = useCallback(() => setVisible(false), [setVisible])

  if (!visible) return null

  return (
    <Root onClick={hide}>
      <Modal onClick={(e) => e.stopPropagation()}>
        {staticTr(t`Challenge number: ${challengeNumber}`)}
        <button onClick={incrementChallengeNumber}>{skipTr("+")}</button>
        <button onClick={decrementChallengeNumber}>{skipTr("-")}</button>
        <div>{staticTr(t`SDK version: ${sdkVersion}`)}</div>
        <div>{staticTr(t`CLI version: ${cliVersion}`)}</div>
      </Modal>
    </Root>
  )
}

const Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  ${flex({ vAlign: "center", hAlign: "center" })};
  background-color: rgba(0, 0, 0, 0.7);
`

const Modal = styled.div`
  background-color: white;
  padding: 20px;
`
