import { t } from "@lingui/macro"
import React, { useMemo, useCallback, useEffect } from "react"
import { useMediaQuery } from "react-responsive"
import styled from "styled-components/macro"
import useFetch from "use-http"

import { staticTr } from "lib/lingui"

import gameRestartImg from "../../../assets/img/icon/gameRestart.png"
import pauseLightImg from "../../../assets/img/icon/pauseLight.png"
import playLightImg from "../../../assets/img/icon/playLight.png"
import settingsLightImg from "../../../assets/img/icon/settingsLight.png"

import { DevSettings, useDevSettings } from "./components/Settings/DevSettings"
import { flex } from "./lib/flex"
import { webTheme } from "./styles/webTheme"
import { BottomBarAction } from "./views/GameView/BottomBar"
import { GameView } from "./views/GameView/GameView"

function useFetchCLIData() {
  return useFetch<{
    cliVersion: string
    gameUrl: string
  }>("/data", {}, [])
}

export function AppCLI() {
  const devSettings = useDevSettings()
  const isDesktop = useMediaQuery({ query: "(min-width: 992px)" })
  const { data } = useFetchCLIData()

  useEffect(() => {
    if (!data) return

    devSettings.setCliVersion(data.cliVersion)
    devSettings.setGame({
      id: 1,
      challengeId: 1,
      nextChallengeAt: null,
      url: `${data.gameUrl}?enableInitialOverlayInBrowser=1`,
      blurredImg:
        "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNs+A8AAgUBgQvw1B0AAAAASUVORK5CYII=",
      logoUrl: require("../../../assets/img/icon/gamesLight.png"),
      title: "Game Title",
      playCount: 99000,
      commentCount: 99000,
    })
    // todo: refactor devSettings so that it changes only when necessary
    // todo: callbacks should not change
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data])

  const openSettings = useCallback(() => devSettings.setVisible(true), [
    devSettings,
  ])

  const bottomBarActions = useMemo<{
    playing: BottomBarAction[]
    paused: BottomBarAction[]
  }>(
    () => ({
      playing: [
        { imgSrc: gameRestartImg, onClick: ({ onRestart }) => onRestart() },
        { imgSrc: pauseLightImg, onClick: ({ onPause }) => onPause() },
        { imgSrc: settingsLightImg, onClick: openSettings },
      ],
      paused: [
        {
          imgSrc: gameRestartImg,
          onClick: ({ onRestart }) => onRestart(),
          disabled: true,
        },
        { imgSrc: playLightImg, onClick: ({ onPlay }) => onPlay() },
        { imgSrc: settingsLightImg, onClick: openSettings },
      ],
    }),
    [openSettings]
  )

  if (!devSettings.game) return null

  const gameView = (
    <GameView
      game={devSettings.game}
      isPortrait
      shouldRenderIframe
      bottomBarActions={bottomBarActions}
      onInit={devSettings.setSdkVersion}
    />
  )

  return (
    <Root>
      {isDesktop && (
        <DesktopDisclaimer>
          {staticTr(
            t`It looks like you're using a desktop device. The game might not be 
            displaying correctly. Please use a mobile device simulator in Chrome, 
            an iOS or Android simulator, or a physical device.`
          )}
        </DesktopDisclaimer>
      )}
      {isDesktop ? <PhoneContainer>{gameView}</PhoneContainer> : gameView}
      <DevSettings />
    </Root>
  )
}

const Root = styled.div`
  width: 100%;
  height: 100%;
  ${flex({ vAlign: "center", hAlign: "center" })};
  background-color: grey;
  > :not(:first-child) {
    margin-top: 20px;
  }
`

const DesktopDisclaimer = styled.div`
  ${webTheme.text.body};
  color: white;
  max-width: 500px;
`

const PhoneContainer = styled.div`
  width: 375px;
  height: 667px;
  border: 10px solid black;
  border-radius: 20px;
  box-sizing: content-box;
  overflow: hidden;
`
