import styled from "styled-components/macro"

import { flex } from "../../lib/flex"

import { GameViewStatus } from "./GameView"
import { ImgButton } from "./ImgButton"

export interface BottomBarAction {
  imgSrc: string
  onClick?: (arg: {
    status: GameViewStatus
    onPause: () => void
    onPlay: () => void
    onRestart: () => void
  }) => void
  disabled?: boolean
}

export function BottomBar({
  status,
  onPause,
  onPlay,
  onRestart,
  disabled,
  actions,
}: {
  status: GameViewStatus
  onPause: () => void
  onPlay: () => void
  onRestart: () => void
  disabled: boolean
  actions: { playing: BottomBarAction[]; paused: BottomBarAction[] }
}) {
  return (
    <Root>
      {actions[status === "playing" ? "playing" : "paused"].map((action) => (
        <ImgButton
          key={action.imgSrc}
          src={action.imgSrc}
          onClick={() =>
            action.onClick?.({ status, onPause, onPlay, onRestart })
          }
          disabled={disabled || action.disabled}
        />
      ))}
    </Root>
  )
}

const Root = styled.div`
  height: 70px;
  background-color: #1c002b;
  ${flex({ horizontal: true, hAlign: "space-around", vAlign: "center" })};
`
